.search-form {
  height: 60px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;

  position: relative;

  display: flex;
  &__column {
    display: flex;
    align-items: center;
    flex: 1 1 25%;
    margin-right: 2px;
    &:last-child {
      margin-right: 0;
    }
  }
  &__label {
    border-color: red;
    width: 100%;
    height: 100%;
  }
  &__input {
    width: 100%;
    height: 100%;
    margin-bottom: 0;
    font-size: 15px;
    font-weight: 400;
    outline: none;
    padding: 0 20px;
    border: none;
  }
  &__input:focus::placeholder {
    opacity: 0;
  }
  &__input:focus:not(.today) {
    border: 2px solid #ff6f32;
  }
  &__column:first-child &__input {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
  }
  &__column:last-child &__input {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
  }
}
